@media screen and (min-width:1200px){
    .container{width:950px; padding:0 10px; margin:0 auto; overflow: hidden; }
    .container-left {width:630px; float: left; overflow: hidden;}
    .container-right {width:280px; float:right; overflow: hidden;} 
    .header-top{
        width:100%;
        min-width: 1200px;
    }  
    .header-top .header-top-m{
        width:960px;
    }
    }
@media screen and (min-width: 768px) and (max-width: 1200px) {
    .container{width:760px; padding:0 10px; margin:0 auto; }
    .container-left {width:100%;}  
    .container-right {display:none;}   

    .header-top{
        width:100%;
    }
    .header-top .header-top-m .header-top-home a{
        display: none;
    }
    }
