        .container{width:100%; padding:10px 0;}
        .container-left {margin:0 10px;}  
        .container-right {display:none;}   
        .header-top{
            width:100%;
            min-width: 100%;
        }
        .header-top .header-top-r{
            display: none;
        }
        .header-top .header-top-m .header-top-search{
            width:30%;
            margin-left:40px;
            background: #ebe9e9;
            padding:10px 20px;
            font-size:14px;
            transition: width .5s;
            -webkit-transition: width .5s;
        }
        .header-top .header-top-m .header-top-home a{
            display: none;
        }
    }
